@import (less) "color.less";

.centre {
  display: table;
  margin-left: auto;
  margin-right: auto;
}

.tabs-container {
  // text-align: center;
  border-collapse: collapse;
 
  .tab-container {
    display: table-cell;
    height: 3.125em;
    width: 12.5em;
    text-align: center;
    background-color: @white;
    border: 1px solid @light_gray;
    // flex-grow: 1;
    // flex-basis: 0;
    padding: 5px;
    cursor: pointer;
    &.active {
      background-color: @green;
      // height: 60px;
      box-shadow: inset 0px 2px 3px 0px rgba(0, 0, 0, 0.2);
      a {
        color: @white;
      }
      i {
        color: @green;
      }
      .tab-title {
        color: @white;
      }
    }
    &.disabled {
      pointer-events: none;
      cursor: not-allowed;
      opacity: 0.5;
      // background-color: #d6d6d6;
    }
    i {
      display: block;
      font-size: 24px;
      color: @gray;
    }
    .tab-title {
      text-transform: uppercase;
      color: @gray;
      display: table-cell;
      height: 2.5em;
      width: 11.875em;
      // line-height: 1;
      font-weight: 600;
      margin: auto;
      vertical-align: middle;
      // margin-top: 10px;
      // margin-bottom:10px;
    }
  }
}


