:host-context(.contrasto-on) {
  .tabs-container {
    display: table;
    border-collapse: collapse;
    .tab-container {
      border-color: @black;
      background-color: @white;
      &.active {
        background-color: @black;
        box-shadow: none;
        i {
          color: @white;
        }
        .tab-title {
          color: @white;
        }
      }
      i {
        color: @black;
      }
      .tab-title {
        color: @black;
      }
    }
  }
}